<template>
  <div class="paymentRoomList">
    <!-- 头部 -->
    <navigation-bar  @onLeftClick="onClickLeft" :pageName="title" :bothWidth="headerItemWidth" >
      <template v-slot:nav-right>
          <div class="rightBtn" :class="{rightBtnThemeL:bindThemeL}" @click="onClickRight">{{rightName}}</div>
      </template>
    </navigation-bar>

    <div class="paymentRoomContent">
      <property-order-unpay ref="unpay"></property-order-unpay>
      <div class="titlePanel" :class="{titlePanelThemeL:bindThemeL}">
        <van-cell title="小区列表" :class="{titleThemeL:bindThemeL}">
           <div class="rightTxt" :class="{titleThemeL:bindThemeL}" @click="deleteFn">{{rightTitle}}</div>
        </van-cell>
      </div>
      <div class="roomListPanel" v-for="(area,index) in areaList" :key="index">
        <div v-bind:class="{'areaHead':area.isShow, 'areaHeadBack':!area.isShow, 'areaHeadThemeL':bindThemeL}" @click="gotoBillList(area)">
            <div class="areaName" :class="{titleThemeL:bindThemeL}">{{area.area_name}}</div>
            <div class="areaImg" :class="{areaImgThemeL:bindThemeL}">
              <img class="orderImg" :src="area.isShow?arrowDownImg:arrowRightImg" />
              <span class="numBadge" :class="{numBadgeThemeL:bindThemeL}" v-if="area.unpayBillNum!='0'">{{area.unpayBillNum}}</span>
            </div>
          </div>
          <div
            v-show="area.isShow==true" class="navPanel"
          >
            <div v-for=" (room,roomIndex) in area.roomList" class="navText" :key="roomIndex">
              <div
                v-if="room.is_owner==0&&isShowDelete==true"
                style="margin-top: 3px;margin-left: 15px;"
              >
                <img width="20" height="20" :src="deleteImg" @click="deleteConfirm(room)" />
              </div>
              <div class="areaLine" :class="{areaLineThemeL:bindThemeL}">{{room.room_name}}</div>
            </div>
          </div>
      </div>
    </div>

    <div class="bottomPanel">
      <div class="addRoom" :class="{titleThemeL:bindThemeL}" @click="addNewRoom">+ 新增缴费</div>
    </div>
  </div>
</template>

<script>
import { queryAreaInfoService,delRoomownerService } from "../../../services/propertyBillService";
import propertyOrderUnpay from "./propertyOrderUnpay";
import { Dialog } from "vant";
import NavigationBar from '@/common/currency/NavigationBar.vue';
export default {
  name: "paymentRoomList",
  mounted() {
    this.queryAreaInfo();
  },
  data() {
    return {
      areaList: [],
      activeNames: ["1"],
      headerItemWidth:{
          width:'110px'
      },
      title:'物业缴费',
      rightName:'缴费记录',
      rightTitle: "编辑",
      isShowDelete: false,
      deleteImg: require("../../../assets/images/home/property/payment/list_btn_delete.png"),
      arrowRightImg: require("../../../assets/images/home/property/payment/icon_arrow_right_black.png"),
      arrowDownImg: require("../../../assets/images/home/property/payment/icon_arrow_down_white.png"),
      /*大字模式*/
      bindThemeL: this.$curTheme==0?false:true
    };
  },
  beforeRouteLeave(to, from, next){
    //清理倒计时
    this.$refs.unpay.clearPayTimer();
    next();
  },
  components: {
    NavigationBar,
    "property-order-unpay": propertyOrderUnpay
  },
  methods: {
    queryAreaInfo() {
      var reqData = {
        owner_code: this.$userInfo.OWNER_CODE
      };
      queryAreaInfoService({"data": JSON.stringify(reqData)})
        .then(result => {
          //请求成功
          console.log(JSON.stringify(result));
          if (result.result) {
            let areas = result.data.areaList;
            for(let index in result.data.areaList){
              if(this.rightTitle == "编辑")
                result.data.areaList[index].isShow = false;
              else
                result.data.areaList[index].isShow = true;
              
            }
            this.areaList = result.data.areaList;
          } else {
            this.$toast("网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
    gotoRoomManager(){
      this.$router.push({
        name: "paymentRoomManager"
      });
    },
    gotoBillList(area){
      this.$router.push({
        name: "propertyBillList",
        params:{areaCode:area.area_code}
      });
    },
    onClickLeft() {
      this.$router.back();
    },
    
    onClickRight() {
      this.$router.push({
        name: "propertyOrderList"
      });
    },
    addNewRoom(){
      this.$router.push('./selectArea')
    },
    /* 房间管理 */
    deleteFn() {
      this.isShowDelete = !this.isShowDelete;
      this.rightTitle = this.rightTitle == "编辑" ? "完成" : "编辑";
      this.areaList.forEach((item, index) => {
      if(this.rightTitle == "编辑")
        item.isShow = false;
        else
        item.isShow = true;
      });
    },
    deleteConfirm(room) {
      Dialog.confirm({
        title: "提示",
        message: "确认解除绑定？"
      })
        .then(() => {
          // on confirm
          this.houseBindingdeleteFn(room);
        })
        .catch(() => {
          // on cancel
        });
    },
    houseBindingdeleteFn(room) {
      console.log(room);
      var reqData = {
        ROOM_CODE: room.room_code,
        OWNER_CODE: room.owner_code,
        USER_ID: this.$userInfo.OWNER_CODE
      };
      delRoomownerService({ data: JSON.stringify(reqData) })
        .then(result => {
          //请求成功
          console.log(JSON.stringify(result));
          if (result.result) {
            this.$toast("您已成功解绑房间");
            this.queryAreaInfo();
          } else {
            this.$toast("网络异常，请稍后重试！");
          }
        })
        .catch(error => {
          //加载失败后，关闭加载等待的文字
          this.$toast("网络异常，请稍后重试！");
        });
    },
  }
};
</script>

<style scoped>
.paymentRoomList {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.paymentRoomList .paymentRoomContent {
  /* padding-top: 20px; */
  flex: 1;
  overflow-y: scroll;
}
.paymentRoomList .rightBtn{
    font-family: PingFangSC-Regular;
    font-size: 16px;
    color: #333333;
    letter-spacing: -0.05px;
    line-height: 32px;
    padding-right: 10px;
    width: 100%;
    text-align: right;
}
.paymentRoomList .rightBtnThemeL {
  font-size: 17px !important;
}
.paymentRoomList .titleThemeL {
  font-size: 18px !important;
}
.paymentRoomList .rightTxt{
  font-size: 14px;
  color: #333333;
}
.paymentRoomList .titlePanel {
  margin-top: 10px;
}
.paymentRoomList .titlePanelThemeL {
  margin: 18px 7px 10px !important;
}
.paymentRoomList .titlePanel .van-cell {
  padding: 0 16px;
}
.paymentRoomList .roomListPanel {
  padding: 5px 16px;
}
.paymentRoomList .roomListPanel .van-cell {
  background: #f6f6f8;
  border-radius: 6px;
  margin: 16px;
  width: auto;
}
.bottomPanel {
  width: 100%;
  background-color: #fff;
  z-index: 100;
}
.bottomPanel .addRoom {
  font-size: 16px;
  color: #fb5768;
  background: #ffffff;
  box-shadow: 0 0 8px 0 rgba(189, 189, 189, 0.29);
  border-radius: 6px;
  height: 55px;
  line-height: 55px;
  text-align: center;
  margin: 16px;
}
/deep/ .van-icon{
  font-size: 20px !important;
}
.numBadge{
    font-size: 10px !important;
    color: #ffffff;
    text-align: center;
    border-radius: 10px;
    background-color: #FC0238;
    width: 20px;
    height: 20px;
    line-height: 22px;
    display:inline-block;
    float: right;
}
.numBadgeThemeL {
  font-size: 11px !important;
  border-radius: 9.5px;
  width: 19px;
  height: 19px;
  line-height: 19px;
}
/* 房间管理相关 */



.paymentRoomList .areaHead {
  background: #fb5364;
  border-radius: 6px;
  height: 48px;
  display: flex;
  line-height: 48px;
  padding-left: 20px;
  color: #fff;
  font-size: 14px;
  position: relative;
}

.paymentRoomList .areaHeadBack {
  background: #f6f6f8;
  border-radius: 6px;
  height: 48px;
  display: flex;
  line-height: 48px;
  padding-left: 20px;
  font-size: 14px;
  position: relative;
}

.paymentRoomList .areaHeadThemeL {
  height: 60px;
  line-height: 60px;
}

.paymentRoomList .areaLine {
  margin-left: 16px;
  font-size: 14px;
  color: #3a3a3a;
}
.paymentRoomList .areaLineThemeL {
  font-size: 18px !important;
}
.paymentRoomList .areaImg {
  text-align: right;
  position: absolute;
  right: 5%;
  margin-top: 13px;
}
.paymentRoomList .areaImgThemeL {
  margin-top: 20px !important;
}
.paymentRoomList .navPanel{
  box-shadow: 0 0 10px 0 rgba(225,225,225,0.50);
  border-radius: 6px;
  z-index: 100;
  margin-top: 10px;
  /* padding: 10px 16px 0 16px; */
}
.paymentRoomList .navText {
  display: flex;
  width: 100%;
  border-bottom: 1px solid #ececec;
  height: 48px;
  line-height: 48px;
  font-size: 14px;
}
.paymentRoomList .orderImg {
    /* width: 20px; */
    height: 20px;
    float: right;
}
</style>
